import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import admin from '@/components/admin'
import luyue from '@/components/lu'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'admin',
      component: admin
    },
    {
      path: '/luyue',
      name: 'luyue',
      component: luyue
    }
  ]
})



