<template>
  <div class="lu">
    <div style="width:100%;">
      <el-carousel indicator-position="outside" >
        <el-carousel-item>
          <img style="height: 100%" src="../assets/1.jpg"/>
        </el-carousel-item>
        <el-carousel-item>
          <img style="height: 100%" src="../assets/2.jpg"/>
        </el-carousel-item>
        <el-carousel-item>
          <img style="height: 100%" src="../assets/3.jpg"/>
        </el-carousel-item>
        <el-carousel-item>
          <img style="height: 100%" src="../assets/4.jpg"/>
        </el-carousel-item>
      </el-carousel>
      <div style="width: 250px;margin-right: auto;margin-left: auto;margin-top: 50px">
        身材:<el-progress :text-inside="true" :stroke-width="18" :percentage="99" color="rgba(142, 113, 199, 0.7)" style="margin-top: 20px"></el-progress>
        美貌：<el-progress :text-inside="true" :stroke-width="18" :percentage="99" status="success" style="margin-top: 20px"></el-progress>
        智商：<el-progress :text-inside="true" :stroke-width="18" :percentage="18" status="exception" style="margin-top: 20px"></el-progress>
      </div>
    </div>

  </div>
</template>

<script>
  import app from "../App";

  export default {
    name: "lu",
    data() {
      return {


      }
    },

    created(){
      this.con();

    },

    methods: {
      con(){
        this.$notify({
          title: '老妈！',
          message: '生日快乐！'
        });
        this.$notify({
          title: '老妈！',
          message: '生日快乐！',
          position: 'bottom-right'
        });
        this.$notify({
          title: '老妈！',
          message: '生日快乐！',
          position: 'bottom-left'
        });
        this.$notify({
          title: '老妈！',
          message: '生日快乐！',
          position: 'top-left'
        });
      }

    }

  }
</script>

<style scoped>
  .lu{
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
</style>




