<template>
  <div class="admin">
    <el-container>
      <el-aside style="width: 290px;  text-align:left;">
        <div style="display: block; position: fixed; left: 0;width: 290px; height:100%">
          <div>
            <el-menu
              class="el-menu-demo"
              mode="horizontal"
              text-color="#FFFFFF"
              background-color="#409EFF"
              collapse-transition
              active-text-color="#ffd04b">
              <el-menu-item index="1">Logo</el-menu-item>
              <el-menu-item index="2">管理界面</el-menu-item>
              <el-menu-item index="3"><router-link to="/" style="text-decoration: none">返回</router-link></el-menu-item>
            </el-menu>
          </div>

          <div style="height:100%">
            <el-menu default-active="1" class="el-menu-vertical-demo">
              <router-link to="/admin" style="text-decoration: none">
                <el-menu-item index="1">
                  <i class="el-icon-menu"></i>
                  <span slot="title" style="align:left">医生申请管理</span>
                </el-menu-item>
              </router-link>
            </el-menu>

          </div>
        </div>
      </el-aside>

      <el-main>
        <div>
          <div style="margin-top:20px;">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item :to="{ path: '/' }">管理员</el-breadcrumb-item>
              <el-breadcrumb-item>医生申请管理</el-breadcrumb-item>
            </el-breadcrumb>
          </div>

          <!--查找框-->
          <div style="margin-top: 40px; padding:16px; background-color: #E9EEF3; text-align: left;">
            <el-form :inline="true" :model="formInline" label-width="65px" style="height:45px" >
              <el-form-item label="用户名:" style="">
                <el-input v-model="formInline.name" size="small" ></el-input>
              </el-form-item>
              <el-form-item style="width:100px">
                <el-button type="primary" size="small" @click="searchByNameNotCompleted(formInline.name)">搜索</el-button>
              </el-form-item>
            </el-form>
          </div>

          <!--表格数据-->
          <div style="margin-top: 40px;">
            <el-table :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
                      max-height="500" height="500" fit border stripe style="width: 100%;">
              <el-table-column prop="userName" label="用户名" sortable></el-table-column>
              <el-table-column prop="userSex" label="性别" sortable></el-table-column>
              <el-table-column prop="userIdentityID" label="身份证号" sortable></el-table-column>
              <el-table-column label="操作" align="center" width="250px" fixed="right">
                <template slot-scope="scope">
                  <el-button type="primary" size="small" @click="approve(scope.$index, scope.row)">批准</el-button>
                  <el-button type="danger" size="small" @click="confirmDeleteNotCompleted(scope.$index, scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>

            <div style="margin-top: 20px; ">
              <el-pagination
                :total="totalSize"
                @current-change="currentChange"
                :page-size="pageSize"
                layout="prev, pager, next, jumper">
              </el-pagination>
            </div>
          </div>

        </div>

      </el-main>
    </el-container>


  </div>
</template>

<script>
  const SecretNumber="wxl123456";
  import app from "../App";

  export default {
    name: "admin",
    data() {
      return {
        //表格数据
        tableData:[{
          userName:'',
          userSex:'',
          userIdentityID:'',
        }],

        //查找框内数据
        formInline: {
          name:''
        },

        formLabelWidth: '120px',

        //当前正在编辑的Index
        currentIndex:'',

        //分页数据
        pageSize:7,
        totalSize:100,
        currentPage:1,//默认开始页面
      }
    },

    created(){
      if(app.methods.getCookie("IsAdminister")==="")
        this.CheckSecretNumber();
      else
         this.loadAllUsers();
    },

    methods: {

      //加载所有医生请求
      loadAllUsers:function() {
        let _this=this;

        this.$axios({
          method:'get',
          url:'/user/allDoctorNotPass',

        }).then(function (response) {
          console.log(response.data);
          _this.$data.tableData = [];
          for(var i=0;i<response.data.length;i++){
            var newItem={
              userName:response.data[i].login_name,
              userSex:(response.data[i].gender.toString()==='m')?'男':'女',
              userIdentityID:response.data[i].identityID,
            };
            _this.$data.tableData.push(newItem);
          }
          _this.$data.totalSize=parseInt(response.data.length);
        }).catch(function (error) {
          console.log(error.response.data)
        })
      },

      //批准医生审核
      approve:function(index, row){
        let _this=this;
        this.$confirm('确定要批准 '+row.userName+' 的申请吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let newIndex=index+((_this.$data.currentPage-1)*(_this.$data.pageSize));//分页要换算当前Index
          let loginName=row.userName;
          this.$axios({
            method:'get',
            url:'/user/check?login_name='+loginName,
          }).then(function (response) {
            console.log(response.data);
            if(response.data.toString()==="1"){
              _this.tableData.splice(index,1);
              _this.$message({
                type: 'success',
                message: '操作成功!'
              });
            }
            else{
              _this.$message({
                type: 'error',
                message: '操作失败!'
              });
            }
          }).catch(function (error) {
            _this.$message({
              type: 'error',
              message: '操作失败!'
            });
            console.log(error.response.data);
          });
        }).catch(() => {
          _this.$message({
            type: 'info',
            message: '已取消批准'
          });
        });
      },

      //删除时弹框确定
      confirmDelete:function(index, row){
        let _this=this;
        this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let newIndex=index+((_this.$data.currentPage-1)*(_this.$data.pageSize));//分页要换算当前Index
          this.handleDelete(newIndex,row);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      confirmDeleteNotCompleted:function(index, row){
        this.$message({
          message: '该功能尚未实现,请联系开发人员~',
          type: 'warning'
        });
      },

      //删除一个商品
      handleDelete:function (index, row) {
        var name=row.name.toString();
        let _this=this;

        this.$axios({
          method:'get',
          url:'/goods/deleteItem?name='+name,
          contentType:'application/json;charset=UTF-8',

        }).then(function (response) {
          _this.tableData.splice(index,1);
        }).catch(function (error) {
          console.log(error.response.data)
        })
      },

      //根据名字搜索商品
      searchByName:function(name){
        let _this=this;

        this.$axios({
          method:'get',
          url:'/goods/selectItemByName?name='+name,

        }).then(function (response) {
          //_this.$data.tableData=response.data;
          console.log(response.data);
          _this.$data.tableData = [];
          for(var i=0;i<response.data.length;i++){
            var newItem={id:response.data[i].goodsid,name:response.data[i].name,picture:'Not Completed',price:parseInt(response.data[i].price),text:response.data[i].text};
            _this.$data.tableData.push(newItem);
            _this.$data.totalSize=_this.$data.tableData.length;//修改表格的totalSize
          }
        }).catch(function (error) {
          console.log(error.response.data)
        })

        // this.$axios({
        //   method:'post',
        //   url:'/goods/selectItemByName',
        //   data:{
        //     name:name
        //   }
        // }).then(function (response) {
        //   console.log(response.data);
        //   _this.$data.tableData = [];
        //   for(var i=0;i<response.data.length;i++){
        //     var newItem={name:response.data[i].name,picture:'Not Completed',price:parseInt(response.data[i].price),text:response.data[i].text};
        //     _this.$data.tableData.push(newItem);
        //   }
        // }).catch(function (error) {
        //   console.log(error.response.data)
        // })

      },
      searchByNameNotCompleted:function(name){
        this.$message({
          message: '该功能尚未实现,请联系开发人员~',
          type: 'warning'
        });
      },

      //输入密码
      CheckSecretNumber(){
        let _this=this;
        this.$prompt('请输入管理员密码', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          if(value===SecretNumber)
          {
            _this.$message({
              type: 'success',
              message: '登录成功',
            });

            _this.IsLogin=true;
            _this.loadAllUsers();
            app.methods.addCookie("IsAdminister","true",0);
          }
          else
          {
            _this.$message({
              type: 'error',
              message: '口令错误，请重新输入',
            });
            _this.CheckSecretNumber();
          }
        }).catch(() => {
          _this.CheckSecretNumber();
        });
      },

      //换页触发的函数
      currentChange:function(currentPage){
        this.currentPage = currentPage;
      }
    }

  }
</script>

<style scoped>
  .admin{
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
</style>




